
$color-blue: #182b89;
$color-lightblue: #3ea6df;

$transition-2s: all 0.2s ease-in-out;

// hero
.wrap-myord-hero {
    width: 100%;
    height: 100vh;
    margin: -80px auto 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0), rgba(24, 43, 136)), url('../../images/BG-38.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
    display: flex;
    align-items: center;
}

.myord-hero-container {
    width: 50%;
    background-color: rgba(238, 238, 238, 0.15);
    border-radius: 0 500px 500px 0;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    display: grid;
    grid-template-columns: 1fr;
    padding: 120px 0;
    align-items: center;
    justify-content: center;

    & > h1 {
        width: 70%;
        margin: 0 auto 20px;
        color: #fff;
        font-size: 36px;
        font-weight: bold;
        letter-spacing: normal;
        line-height: 1.5;
        display: grid;
        grid-template-columns: 1fr;
        & > span {
            font-size: 50px;
        }
    }

    & > p {
        width: 70%;
        margin: 0 auto;
        color: #fff;
        font-size: 24px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.29;
        letter-spacing: normal;
    }
}


.myord-header {
    width: 80%;
    max-width: 1600px;
    margin: 0 auto 60px;
    padding: 0 0 0 20px; 
    border-left: 20px solid $color-blue;

    & .myord-breadcrumbs {
        display: flex;

        & p {
            color: #aeaeae;
            font-size: 22px;
            font-weight: bold;
            letter-spacing: 0.5px;
            text-decoration: none;
            text-transform: capitalize;
            transition: $transition-2s;
        }

        & .myord-breadcrumbs-divider {
            color: #aeaeae;
            margin: 0 10px;
            font-size: 22px;
        }

        & h2 {
            color: $color-blue;
            font-size: 22px;
            font-weight: bold;
            letter-spacing: 0.5px;
            margin: 0;
            line-height: initial;
            text-transform: capitalize;
        }
    }
    
}


// content 
.myord-container {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto ;

    & .ant-empty {
        margin: 60px 0 90px;
        & span {
            color: #abb8c1;
        }
    }

    & .pagination {
        margin: 0 auto 40px;
        text-align: center;
        justify-content: center;
    }
}

.orders-antd-pagination {
    margin: 0 auto 40px;
    text-align: center;
    justify-content: center;

    & .anticon {
        vertical-align: 0 !important;
    }

    & .ant-pagination-simple-pager {
        color: $color-blue !important;
    }
}


// accordion
.myord-accord-1 {
    background-color: transparent;
    border: none;
    border-radius: 0;

    & .accordion-item {
        margin: 0 auto 50px;
        border-radius: 15px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
    }

    & .accordion-button {
        background: transparent;
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        // gap: 10px;

        &:focus {
            box-shadow: none;
        }
        &::after {
            // display: none;
            color: $color-blue;
            margin: 0 auto;
            background: url("../../images/chevron-down-01.png");
            background-size: 100%;
        }
        &:not(.collapsed)::after {
            background: url("../../images/chevron-down-02.png");
            background-size: 100%;
        }
    }

    & .accordion-button:not(.collapsed) {
        box-shadow: none;
    }
}

.myord-accord-header {
    width: 100%;

    & .myord-a-h-item-1 {
        width: 100%;
        display: flex;
        justify-content: space-between;

        & p {
            color: $color-blue;
            font-size: 18px;
            font-weight: bold;
        }

        & .myord-order-status {
            color: #fff;
            height: fit-content;
            padding: 7px 14px;
            margin: 0;
            font-size: 12px;
            font-weight: normal;
            border-radius: 6px;
            text-transform: capitalize;
            &.completed {
                background-color: #30d158;
            }
            &.cancelled {
                background-color: #ff3b30;
            }
            &.pending {
                background-color: #ffaa00;
            }
            &.to_ship {
                background-color: $color-lightblue;
            }
        }
    }

    & .myord-a-h-item-2 {
        display: flex;
        align-items: center;
        margin: 0;

        & figure {
            max-width: 350px;
            margin: 0 50px 0 0;
            & img {
                max-width: 350px;
                border-radius: 8px;
            }
        }

        & .myord-a-h-item-2-1 {
            & p {
                color: $color-blue;
                font-size: 18px;
                font-weight: normal;
                margin: 0 0 10px;
            }
            & h3 {
                color: $color-blue;
                font-size: 24px;
                font-weight: bold;
                margin: 0 0 10px;
            }
            & .myord-a-h-item-2-1-country {
                color: #989898;
                font-size: 18px;
                font-weight: normal;
                display: flex;
                align-items: center;

                & > .myord-a-h-item-2-1-country-icon {
                    color: #989898;
                    font-size: 20px;
                    margin-right: 8px;
                    & path {
                        stroke: #989898;
                    }
                }
            }
        }
    }

    & .myord-a-h-linediv {
        margin: 10px 0 0;
        border-bottom: 1px solid #d9d9d9;
    }

    & .myord-a-h-item-3 {

        & .myord-a-h-item-3-1 {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 20px 0 10px;

            & .myord-a-h-item-3-1-p1 {
                color: $color-blue;
                font-size: 18px;
                font-weight: 600;
            }

            & .myord-a-h-item-3-1-p2 {
                color: $color-lightblue;
                font-size: 24px;
                font-weight: 600;

                &.pending {
                    color: #ffaa00;
                }
                &.cancelled {
                    color: #ff3b30;
                }
                &.to_ship {
                    color: $color-lightblue;
                }
            }
        }
    }

}

.myord-accord-body {
    padding: 16px 20px 30px;
    
    .myord-item-attribute {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 20px;

        & .myord-attri_item {
            padding: 20px 10px;
            text-align: center;
            border-radius: 15px;
            box-shadow: 0 0 5px 0 rgba(62, 166, 223, 0.3) !important;
            -moz-box-shadow: 0 0 5px 0 rgba(62, 166, 223, 0.3) ;
            -webkit-box-shadow: 0 0 5px 0 rgba(62, 166, 223, 0.3);

            & > img {
                width: 40px;
                height: 40px;
                margin: 0 auto 10px;
            }

            & > p {
                color: $color-blue;
                font-size: 12px;
                font-weight: bold;
                margin: 0 auto 5px;
            }

            & > h5 {
                color: #989898;
                font-size: 11px;
                font-weight: normal;
            }
        }
    }
}



@media screen and (min-width: 1901px) {
    .myord-hero-container {
        padding: 180px 0;
    }

    .myord-header {
        width: 80%;
        max-width: 1600px;
        margin: 0 auto 60px ;
    }
}


@media screen and (max-width: 1200px) {
    .wrap-myord-hero {
        height: 800px;
    }

    .myord-hero-container {
        width: 60%;
        padding: 120px 0;

        & > h1 {
            font-size: 32px;
            display: initial;

            & > span{
                font-size: 42px;
            }
        }
        & > p {
            font-size: 20px;
        }
    }

    .myord-header{
        width: 90%;
        margin: 0 auto 30px;

        & .myord-breadcrumbs {
            flex-wrap: wrap;

            & p {
                font-size: 18px;
            }
            & h2 {
                width: fit-content;
                font-size: 18px;
            }
            & .myord-breadcrumbs-divider {
                font-size: 18px;
            }
        }
    }

    .myord-accord-body {
        & .myord-item-attribute { 
            gap: 15px;
            & .myord-attri_item {
                padding: 20px 0;
            }
        
        }
    }
}



@media screen and (max-width: 1024px) {
    .wrap-myord-hero {
        height: 700px;
    }
    .myord-hero-container {
        width: 65%;
        padding: 90px 0;

        & > h1 {
            font-size: 32px;
            display: initial;

            & > span{
                font-size: 42px;
            }
        }
        & > p {
            font-size: 20px;
        }
    }

    .myord-container {
        width: 90%;
    }

    .myord-accord-header .myord-a-h-item-2 {
        display: grid;
        grid-template-columns: 2fr 3fr;
        gap: 30px;
        & figure {
            max-width: 100%;
            margin: 0;
            & img {
                width: 100%;
                max-width: 100%;
            }
        }

        & .myord-a-h-item-2-1  {
            & p {
                font-size: 16px;
            }
            & h3 {
                font-size: 20px;
            }
            & .myord-a-h-item-2-1-country {
                font-size: 16px;
                &  > .myord-a-h-item-2-1-country-icon {
                    font-size: 18px;
                }
            }
        }
    }

    .myord-accord-body {
        & .myord-item-attribute { 
            gap: 10px;
            & .myord-attri_item {
                padding: 20px 8px;
            }
        
        }
    }

    .myord-accord-header .myord-a-h-item-3 {
        & .myord-a-h-item-3-1 {
            margin: 10px 0;
            & .myord-a-h-item-3-1-p1 {
                font-size: 16px;
            }
            & .myord-a-h-item-3-1-p2 {
                font-size: 20px;
            }
        }
    }

}



@media screen and (max-width: 576px) {
    .myord-hero-container {
        width: 80%;
        padding: 50px 0;

        & > h1 {
            font-size: 24px;

            & > span{
                font-size: 30px;
            }
        }
        & > p {
            font-size: 16px;
        }
    }

    .myord-header{
        width: 90%;
        margin: 0 auto 30px;

        & .myord-breadcrumbs {
            font-size: 14px;
            flex-wrap: wrap;
        }
    }
    .myord-item-header  {

        & > p {
            font-size: 16px;
            margin-bottom: 0px;
        }

        & > h4 {
            font-size: 20px;
        }

        & > .cars-card-item-country {
            font-size: 14px;

            & > .cars-card-item-country-icon {
                font-size: 20px;
            }
        }
    }

    .myord-accord-header .myord-a-h-item-1 {
        & .myord-order-status {
            font-size: 10px;
        }
    }

    .myord-accord-header .myord-a-h-item-2 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;

        & .myord-a-h-item-2-1  {
            & p {
                font-size: 12px;
            }
            & h3 {
                font-size: 16px;
            }
            & .myord-a-h-item-2-1-country {
                font-size: 12px;
                &  > .myord-a-h-item-2-1-country-icon {
                    font-size: 16px;
                    margin-right: 5px;
                }
            }
        }
    }


    .myord-accord-header .myord-a-h-item-3 {
        & .myord-a-h-item-3-1 {
            margin: 10px 0;
            & .myord-a-h-item-3-1-p1 {
                font-size: 12px;
            }
            & .myord-a-h-item-3-1-p2 {
                font-size: 14px;
            }
        }
    }

    .myord-accord-1 {
        & .accordion-item {
            margin-bottom: 30px;
        }
    }
    .myord-accord-body {
        padding: 16px 20px 30px;
        
        .myord-item-attribute {
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 10px;

            & .myord-attri_item {
                padding: 15px 10px 10px;
                border-radius: 10px;

                & > img {
                    width: 30px;
                    height: 30px;
                }
                & > p {
                    font-size: 9px;
                }
                & > h5 {
                    font-size: 9px;
                }
            }
        }
    }

    .orders-antd-pagination {
        margin-bottom: 30px;
    
        & .ant-pagination-simple-pager {
            font-size: 12px;
        }
    }
}