                width: 30px;
                height: 30px;
            }
            & > p {
                font-size: 9px;
            }
            & > h5 {
                font-size: 9px;
            }
        }
    }

    .sgcars-item-dutylist {
        & > h5 {
            font-size: 16px;
        }
        
        & .sgcars-item-dutylist-table {
            border-radius: 15px;
        }
        & .sgcars-item-dutylist-table thead {
            font-size: 12px;
        }
        & .sgcars-item-dutylist-table td {
