        }
    }

    .sgcars-loan-sec-1 {
        // grid-template-columns: 1fr;
        // gap: 20px;
        // margin: 0 0 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 20px;
        & .sgcars-loan-item-1 {
            width: 100%;
        }
        & .mob-sgcars-loan-item-2 {
            width: 47%;
        }
    }

    .sgcars-loan-item-1 {
        & p {
            font-size: 12px;
        }
    }

    .sgcars-loan-item-2 {
        & p {
            font-size: 12px;
        }
    }

    .sgcars-calculate-modal .modal-dialog{
        width: 95%;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    .sgcars-calculate-modal .modal-header {
        padding: 16px 20px;
    }
    .sgcars-calculate-modal .modal-body {
        padding: 0 20px;
    }
    .sgcars-calculate-modal .modal-footer {
        padding: 0 20px 20px;
    }

    .sgcars-accord-roadtax {
        & .fin-tab-pane-2-content > .fin-tab-pane-2-input > .fin-tab-pane-2-saloon-type  {
            padding: 0 !important;
            gap: 20px;
        }
    }
    .sgcars-accord-etc .sgcars-accord-item .sgcars-accord-roadtax .fin-tab-pane-2-saloon-type-1 > h5 {
        font-size: 12px;
    }


    .sgcars-calculate-modal.mpayment-modal {
        padding: 0;
        & .modal-dialog {
            width: 95%;
            max-width: 900px;
            margin: 100px auto 50px;
        }
    
        & .pcars-container {
            width: 100%;
        }
        & .modal-footer {
            padding: 0 0 30px;
            width: 85%;
            display: grid;
            grid-template-columns: 1fr;
            gap: 20px;
            margin: 0 auto;
    
            & button:nth-child(2) {
                background-color: rgb(216, 75, 75);
            }
        }
    }

    .sgcars-calculate-modal .sgcars-modalbody-3 {
        grid-template-columns: 1fr;
    }

    // payment modal
    .sgcars-calculate-modal.mpayment-modal {
        & .pcars-price-summary {
            & .pcars-summary-item-2 {
                & > h5 {
                    font-size: 14px;
                }

                & .pcars-summary-item-2-wrap {
                    & > p {
                        font-size: 12px;
                    }
                }
            }
        }
    }
    .sgcars-calculate-modal.mpayment-modal .pcars-payment-method > h5, 
    .sgcars-calculate-modal.mpayment-modal .pcars-payment-details > h5 {
        font-size: 14px;
        font-weight: 600;
    }

    .sgcars-calculate-modal.mpayment-modal .pcars-payment-details .pcars-payment-details-item .pcars-payment-details-item-1 .pcars-payment-details-item-1-1, 
    .sgcars-calculate-modal.mpayment-modal .pcars-payment-details .pcars-payment-details-item .pcars-payment-details-item-2 .pcars-payment-details-item-2-1 {
        font-size: 13px;
    }
    
    .sgcars-calculate-modal.mpayment-modal .pcars-payment-details .pcars-payment-details-item .pcars-payment-details-item-1 .pcars-payment-details-item-1-2, 
    .sgcars-calculate-modal.mpayment-modal .pcars-payment-details .pcars-payment-details-item .pcars-payment-details-item-2 .pcars-payment-details-item-2-2 {
        font-size: 15px;
    }

